@use 'base/variables' as v;
@use 'base/mixins' as mix;

.footer{
    background-color: v.$azul;
    margin-top: 5rem;
    padding-top: 3rem;
    color: v.$blanco;

    &__logo {
        @include mix.logo;
        @include mix.texto-fluido(2rem,4rem);
        color: v.$blanco;
    }
    &__grid {
        @include mix.contenedor;
        @include mix.tablet {
            @include mix.grid(2,3rem);
        }
    }
    &__texto {
        line-height: 2;
        text-align: center;
        @include mix.tablet {
            text-align: left;
        }
    }
    &__copyright {
        margin: 3rem 0 0 0;
        padding: 2rem 0;
        font-size: 1.4rem;
        text-align: center;
        background-color: darken(v.$azul,15);
        font-weight: v.$black;
        &--regular {
            font-weight: v.$regular;
        }
    }
}