@use 'base/variables' as v;
@use 'base/mixins' as mix;

.header {
    min-height: 60rem;
    background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, rgba(v.$negro,1), rgba(v.$negro,.5)), image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
    background-repeat: no-repeat;
    background-size: 400px,400px,cover,cover;
    background-position: top left, bottom right, center center, center center;

    &__contenedor {
        @include mix.contenedor;
    }

    &__navegacion {
        padding: 5rem 0;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 2rem;

        @include mix.tablet {
            justify-content: flex-end;
        }
    }

    &__enlace {
        color: v.$blanco;
    }

    &__contenido {
        padding-top: 10rem;
        @include mix.tablet {
            padding-top: 5rem;
        }
    }

    &__logo {
        @include mix.texto-fluido(2rem,8rem);
        @include mix.texto-degradado(v.$azul,v.$secundario);
        text-align: center;
        
        @include mix.tablet{
            display: inline-block;
            text-align: left;
        }
    }

    &__texto {
        color: v.$blanco;
        text-align: center;
        font-size: 3rem;
        margin: 2rem 0;
        font-weight: v.$bold;

        @include mix.tablet{
            text-align: left;
        }

        &--modalidad {
            font-size: 2rem;
        }
    }

    &__boton {
        @include mix.boton(v.$secundario,v.$blanco);
        margin: 2rem 0;
        @include mix.tablet {
            width: auto;
        }
    }

}