@use 'base/variables' as v;
@use 'base/mixins' as mix;

.barra{
    background-color: v.$azul;

    &__contenido {
        @include mix.contenedor;
        display: flex;
        flex-direction: column;
        @include mix.desktop {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }

    &__logo {
        @include mix.logo;
    }
}