@use 'base/variables' as v;
@use 'base/mixins' as mix;

.navegacion {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    @include mix.desktop {
        flex-direction: row;
        margin: 0;
    }

    &__enlace{
        color: v.$blanco;
        font-size: 1.5rem;
        padding: 1.8rem;
        font-weight: v.$black;
        text-transform: uppercase;
        text-align: center;

        &:hover {
            background-color: v.$blanco;
            color: v.$azul;
        }

    }
}